@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
.box{display:flex}.box--a-center{align-items:center}.box--a-end{align-items:flex-end}.box--a-start{align-items:flex-start}.box--d-column{flex-direction:column}.box--d-row{flex-direction:row}.box--j-center{justify-content:center}.box--j-end{justify-content:flex-end}.box--j-space-around{justify-content:space-around}.box--j-space-between{justify-content:space-between}.box--j-start{justify-content:flex-start}

.card{align-items:center;background-color:#009688;border:2px solid #37474f;display:flex;flex-direction:column;justify-content:center;width:800px}.card__header{border-bottom:2px solid #37474f;padding:0.75rem 1rem;width:100%}.card__body{padding:1rem;width:100%}

.button{border:2px solid #37474f;font-family:inherit;font-size:1rem;padding:0.5rem}.button:hover,.button:active{cursor:pointer}.button:disabled{color:black;cursor:not-allowed;opacity:0.65}.button--danger{background-color:#ff5252}.button--success{background-color:#b2ff59}

.error-message{margin-top:0.5rem}.error-message__icon{height:20px;margin-right:0.5rem;width:20px}.error-message__text{font-size:0.85rem;font-weight:bold}

.textarea{border:2px solid #37474f;font-family:inherit;font-size:1rem;padding:0.5rem;resize:vertical;width:100%}.textarea--valid:focus{outline-color:#b2ff59}.textarea--invalid:focus{outline-color:#ff5252}

.add-text__title{margin:0;text-align:center}.add-text__form-group{margin:0.75rem 0}.add-text__form-group:first-child{margin-top:0}.add-text__form-group:last-child{margin-bottom:0}.add-text__text-field{min-height:100px;max-height:500px}.add-text__button-group{margin-top:0.75rem}.add-text__error{font-size:0.85rem;font-weight:bold;margin-top:0.5rem}

.app{height:100%;padding:1rem}.app__tabs{width:800px}.app__tabs button{width:50%}.app__tabs button:first-child{border-right-width:1px}.app__tabs button:last-child{border-left-width:1px}

.stats__done{color:#ff0;font-weight:bold;text-decoration:underline}

.trainer__text{background-color:#f5f5f5;border:2px solid #37474f;padding:0.5rem;text-align:left}.trainer__char--correct{background-color:#b2ff59}.trainer__char--incorrect{background-color:#ff5252}.trainer__input-field{min-height:100px;max-height:400px}

body{background-color:#80cbc4;font-family:'JetBrains Mono', monospace;margin:0}html,body,#root{height:100%}*{box-sizing:border-box}p{margin:0}.mt-1{margin-top:0.5rem}.mt-2{margin-top:1rem}.mt-3{margin-top:1.5rem}.mt-4{margin-top:2rem}.mb-1{margin-bottom:0.5rem}.mb-2{margin-bottom:1rem}.mb-3{margin-bottom:1.5rem}.mb-4{margin-bottom:2rem}.mr-1{margin-right:0.5rem}.mr-2{margin-right:1rem}.mr-3{margin-right:1.5rem}.mr-4{margin-right:2rem}.mx-1{margin:0.5rem 0}.mx-2{margin:1rem 0}.mx-3{margin:1.5rem 0}.mx-4{margin:2rem 0}

